<template>
  <div class="contain-header">
    <div class="header-main">
      <div class="header-btn media-header-main ">
        <a @click="changClose"><i :class="icon"></i></a>
      </div>
      <ul class="media-header-main">
        <li>
          <a href="https://fanyi.youdao.com/" target="_blank"
            ><i class="bx bxs-analyse bx-sm"></i>
            <div class="btn-text">翻译</div>
          </a>
        </li>
        <li>
          <a href="https://weather.cma.cn/" target="_blank"
            ><i class="bx bx-cloud-snow bx-sm"></i>
            <div class="btn-text">天气</div>
          </a>
        </li>
        <li>
          <a href="https://www.bilibili.com/" target="_blank"
            ><i class="bx bx-tv bx-sm"></i>
            <div class="btn-text">B站</div>
          </a>
        </li>
        <li>
          <a href="https://www.apple.com.cn/" target="_blank"
            ><i class="bx bxl-apple bx-sm"></i>
            <div class="btn-text">Apple</div>
          </a>
        </li>
        <li>
          <a href="https://github.com/" target="_blank"
            ><i class="bx bx-message-square-dots bx-sm"></i>
            <div class="btn-text">个人社区</div>
          </a>
        </li>
      </ul>
      <div class="media-header medied-header">
        <img :src="checkedImg" alt="emptyacup" style="height: 45px;" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "mainHeader",
  data() {
    return {
      isClose: true,
      icon: "bx bx-right-indent bx-fade-right-hover bx-sm",
      checkedImg: "http://png.eot.ooo/i/2022/05/01/626e55bd40508.png",
    };
  },
  mounted() {
    this.$bus.$on("cSun", this.changeSun);
  },
  methods: {
    changClose() {
      this.isClose = !this.isClose;
      var close = this.isClose;
      this.$bus.$emit("cClose", close);
      if (close) {
        this.icon = "bx bx-right-indent bx-fade-right-hover bx-sm";
      } else {
        this.icon = "bx bx-left-indent bx-fade-left-hover bx-sm";
      }
    },
    changeSun(sun) {
      this.checkedImg = sun
        ? "http://png.eot.ooo/i/2022/05/01/626e55bd40508.png"
        : "http://png.eot.ooo/i/2022/09/10/631c20dea2089.png";
    },
  },
};
</script>

<style lang='less' scoped>
.contain-header {
  position: fixed;
  width: 100%;
  height: 74px;
  z-index: 5;
  top: 0;
  .header-main {
    height: 100%;
    display: flex;
    align-items: center;
    flex-basis: auto;
    position: relative;
    background-color: var(--header-color);
    .header-btn {
      height: 100%;
      width: 74px;
      text-align: center;
      a {
        display: block;
        height: 100%;
        border-radius: 8px;
        text-align: center;
        line-height: 74px;
        &:hover {
          background-color: var(contain-color);
        }
        i {
          position: relative;
          top: 10%;
        }
      }
    }
    ul {
      display: flex;
      align-items: center;
      height: 100%;
      li {
        height: 42px;
        padding: 5px 15px;

        a {
          display: flex;
          height: 100%;
          border-radius: 8px;
          align-content: center;
          text-decoration: none;
          i {
            margin: 4px;
          }
          .btn-text {
            text-align: center;
            margin: auto;
          }
        }
      }
    }
    @media screen and (max-width: 768px) {
      .media-header-main {
        display: none;
      }
      .medied-header {
        display: flex;
        width: 100%;
        align-items: center;
        height: 100%;
        justify-content: center;
      }
    }
  }
  .media-header {
    display: none;
  }
}
</style>