<script>
export default {
  name: "mainAside",
  data() {
    return {
      isClose: "aside close",
      imgSrc: "http://png.eot.ooo/i/2022/09/10/631c2f28e5a3c.png",
      checkedImg: "http://png.eot.ooo/i/2022/05/01/626e55bd40508.png",
      // close的状态
      closing: true,
      // menu开关
      isChildMenu: false,
      isChildMenuItems: {
        MenuItem1: false,
        MenuItem2: false,
        MenuItem3: false,
      },
    };
  },
  mounted() {
    this.$bus.$on("cClose", this.changeClose);
    this.$bus.$on("cSun", this.changeSun);
  },
  methods: {
    changeClose(close) {
      if (close) {
        this.isClose = "aside close";
        this.imgSrc = "http://png.eot.ooo/i/2022/09/10/631c2f28e5a3c.png";
        this.isChildMenu = false;
      } else {
        this.isClose = "aside";
        this.imgSrc = this.checkedImg;
      }
      this.closing = close;
    },
    changeSun(sun) {
      this.checkedImg = sun
        ? "http://png.eot.ooo/i/2022/05/01/626e55bd40508.png"
        : "http://png.eot.ooo/i/2022/09/10/631c20dea2089.png";
      this.changeClose(this.closing);
    },
    handleChildMenu(childMenu) {

      if (this.closing) {
        this.isChildMenu = false;
      } else {
        this.isChildMenu = true;
        this.isChildMenuItems[childMenu] = !this.isChildMenuItems[childMenu]
        console.log(this.isChildMenuItems[childMenu])
      }
    },
  },
};
</script>

