<template>
  <div class="fix-footer">
    <a @click="changeSun"><i :class="icon"></i> </a>

    <a href=""><i class="bx bx-search-alt"></i></a>
    <a href=""><i class="bx bx-bookmark-heart"></i></a>
    <a href=""><i class="bx bxs-chevrons-up"></i></a>
  </div>
</template>

<script>
export default {
  name: "fixFooter",
  data(){
    return {
      icon:"bx bxs-moon",
      isSun:true,
    }
  },
  methods:{
    changeSun(){
      this.isSun = !this.isSun
      let sun = this.isSun
      this.$bus.$emit('cSun',sun)
      this.icon = sun ? "bx bxs-sun" : "bx bxs-moon"
    }
  }
};
</script>

<style lang="less" scoped>
.fix-footer {
  position: fixed;
  bottom: 20px;
  right: 10px;
  display: flex;
  z-index: 5;
  flex-direction: column-reverse;
  a {
    margin: 5px;
    width: 40px;
    height: 40px;
    font-size: 1rem;
    text-align: center;
    line-height: 40px;
    color: var(--fixfootert-font-color);
    background: var(--fixfootert-color);
    border-radius: 50%;
    position: relative;
    // &:hover{
    //       color: rgba(39, 39, 39, 0.9);
    // }
  }
  
}
</style>