<template>
  <div :class="all">
    <mainAside></mainAside>
    <div :class="main" class="media-main">
      <main-header></main-header>
      <main-home></main-home>
    </div>
    <fix-footer></fix-footer>
  </div>
</template>

<script>
import mainAside from "./components/mainAside.vue";
import mainHeader from "./components/mainHeader.vue";
import mainHome from "./components/mainHome.vue";
import fixFooter from "./components/fixFooter.vue";
export default {
  name: "App",
  components: { mainAside, mainHeader, mainHome, fixFooter },
  data() {
    return {
      main: "main main-close",
      all: "all",
    };
  },
  mounted() {
    this.$bus.$on("cClose", this.changeClose);
    this.$bus.$on("cSun", this.changeSun);
  },
  methods: {
    // 改变aside 的开关
    changeClose(close) {
      if (close) {
        this.main = "main main-close";
      } else {
        this.main = "main";
      }
    },
    // sun的开关
    changeSun(sun) {
      this.all = sun ? "all" : "all dark";
    },
  },
};
</script>





